import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { signOut, getCurrentUser, fetchAuthSession } from '@aws-amplify/auth';
import { get, post } from '@aws-amplify/api';
import HamburgerMenu from './HamburgerMenu';
import styles from './SendCoin.module.css';

const cloudFrontUrl = 'https://d26ws69lscjxgo.cloudfront.net';

export default function SendCoin() {
  const navigate = useNavigate();
  const [currentBalance, setCurrentBalance] = useState(0);
  const [totalSent, setTotalSent] = useState(0);
  const [recipientUsername, setRecipientUsername] = useState('');
  const [recipientId, setRecipientId] = useState('');
  const [amount, setAmount] = useState('');
  const [message, setMessage] = useState('');
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [transactions, setTransactions] = useState([]);
  const [currentUserInfo, setCurrentUserInfo] = useState(null);
  const [userList, setUserList] = useState([]);
  const [userNames, setUserNames] = useState([]);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');

  // 
  const fetchCurrentUser = async () => {
    try {
      const user = await getCurrentUser();
      setCurrentUserInfo(user);
    } catch (error) {
      console.error(':', error);
    }
  };

  // 
  const fetchUserData = async () => {
    try {
      const session = await fetchAuthSession();
      const token = session.tokens.idToken.toString();
      const shortUserId = session.userSub.split('-')[0];

      const response = await fetch(`${cloudFrontUrl}/api/user?userId=${shortUserId}`, {
        method: 'GET',
        headers: {
          Authorization: `Bearer ${token}`,
          'Content-Type': 'application/json'
        }
      });

      if (!response.ok) {
        throw new Error('');
      }

      const result = await response.json();
      const { userInfo, allUsers } = result;

      if (userInfo) {
        setCurrentUserInfo({
          userId: userInfo.userId,
          username: userInfo.name
        });
        setCurrentBalance(userInfo.balance);
        setTotalSent(userInfo.total);
      }

      if (Array.isArray(allUsers)) {
        // 
        setUserList(allUsers);  // 
        // 
        setUserNames(allUsers.map(user => user.name));  // state
      }

    } catch (error) {
      console.error(':', error);
      setError('');
    }
  };

  // 
  const fetchTransactions = async () => {
    try {
      const session = await fetchAuthSession();
      const token = session.tokens.idToken.toString();
      const shortUserId = session.userSub.split('-')[0];  // CognitoID

      const response = await fetch(`${cloudFrontUrl}/api/transactions?userId=${shortUserId}`, {  // ID
        method: 'GET',
        headers: {
          Authorization: `Bearer ${token}`,
          'Content-Type': 'application/json'
        }
      });

      if (!response.ok) {
        throw new Error('');
      }

      const result = await response.json();
      setTransactions(result.transactions || []);

    } catch (error) {
      console.error(':', error);
      setError('');
    }
  };

  // 
  useEffect(() => {
    const loadData = async () => {
      try {
        await fetchUserData();
        await fetchTransactions();
      } catch (error) {
        console.error(':', error);
      }
    };
    
    loadData();
  }, []);

  const handleRecipientChange = async (event) => {
    const username = event.target.value;
    console.log(':', username);

    setRecipientUsername(username);
    try {
      const session = await fetchAuthSession();
      const token = session.tokens.idToken.toString();

      // nameID
      const selectedUser = userList.find(user => user.name === username);
      if (selectedUser) {
        console.log('recipientId:', selectedUser.userId, 'for username:', username);
        setRecipientId(selectedUser.userId);
      } else {
        console.error(':', username);
        setError('');
        setRecipientId('');
      }
    } catch (error) {
      console.error('ID:', error);
      setRecipientId('');
    }
  };

  const handleAmountChange = (event) => {
    setAmount(event.target.value);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setSuccess('');
    setIsLoading(true);
    setMessage('');

    if (!recipientId) {
      setError('');
      setIsLoading(false);
      return;
    }

    // 
    if (currentUserInfo.userId === recipientId) {
      setError('');
      setIsLoading(false);
      return;
    }

    try {
      const session = await fetchAuthSession();
      const token = session.tokens.idToken.toString();

      const requestBody = {
        senderId: currentUserInfo.userId,
        recipientId: recipientId,
        amount: Number(amount)
      };

      console.log(':', requestBody);

      const response = await fetch(`${cloudFrontUrl}/api/send`, {  // 
        method: 'POST',
        headers: {
          Authorization: `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(requestBody)
      });

      if (!response.ok) {
        const errorDetails = await response.json();
        console.error('API call failed:', errorDetails);
        throw new Error(`API call failed: ${response.statusText}`);
      }

      const result = await response.json();
      console.log(':', result);
      setSuccess(`${recipientUsername}`);
      setRecipientUsername('');
      setAmount('');
      await fetchUserData();
      await fetchTransactions();

    } catch (error) {
      console.error(':', error);
      setError('');
    } finally {
      setIsLoading(false);
    }
  };

  const handleSignOut = async () => {
    try {
      setIsLoading(true);
      await signOut({ global: true });
      localStorage.clear();
      navigate('/', { replace: true });
      window.location.reload();
    } catch (error) {
      console.error(':', error);
      setError('');
    } finally {
      setIsLoading(false);
    }
  };

  const confirmSignOut = () => {
    if (window.confirm('')) {
      handleSignOut();
    }
  };

  // 
  const renderTransactionHistory = () => {
    return transactions.map((tx) => (
      <div key={tx.transactionId} className={styles.transaction}>
        <div className={styles.transactionType}>
          {tx.type === 'sent' ? '' : ''}
        </div>
        <div className={styles.transactionDetails}>
          <span className={styles.partner}>
            {tx.type === 'sent' ? `: ${tx.partnerId}` : `: ${tx.partnerId}`}
          </span>
          <span className={styles.timestamp}>
            {new Date(tx.timestamp).toLocaleString()}
          </span>
        </div>
      </div>
    ));
  };

  return (
    <div className={styles.container}>
      <header className={styles.header}>
        <div className={styles.headerLeft}>
          <HamburgerMenu isOpen={isMenuOpen} toggleMenu={() => setIsMenuOpen(!isMenuOpen)} />
          <h1 className={styles.headerTitle}></h1>
        </div>
        <button 
          onClick={confirmSignOut} 
          className={styles.signOutButton}
          disabled={isLoading}
        >
          {isLoading ? '...' : ''}
        </button>
      </header>

      <div className={styles.card}>
        <div className={styles.userInfo}>
          <h3></h3>
          <p className={styles.userDetail}>
            <span className={styles.userLabel}>ID:</span>
            <span className={styles.userId}>{currentUserInfo?.userId}</span>
          </p>
          <p className={styles.userDetail}>
            <span className={styles.userLabel}>:</span>
            <span className={styles.username}>{currentUserInfo?.username}</span>
          </p>
        </div>
        <div className={styles.balanceInfo}>
          <div className={styles.balance}>
            <h3></h3>
            <p className={styles.balanceAmount}>
              {currentBalance}
              <span className={styles.currency}>DGC</span>
            </p>
          </div>
          <div className={styles.balance}>
            <h3></h3>
            <p className={styles.balanceAmount}>
              {totalSent}
              <span className={styles.currency}>DGC</span>
            </p>
          </div>
        </div>
      </div>

      <div className={styles.card}>
        <h2 className={styles.title}>DigiCoin</h2>
        {error && <p className={styles.error}>{error}</p>}
        {success && <p className={styles.success}>{success}</p>}
        
        <form onSubmit={handleSubmit} className={styles.form}>
          <div className={styles.formGroup}>
            <label htmlFor="recipient" className={styles.label}>
              :
            </label>
            <select
              id="recipient"
              value={recipientUsername}
              onChange={handleRecipientChange}
              required
              className={styles.input}
              disabled={isLoading}
            >
              <option value=""></option>
              {userNames.map(name => (
                <option key={name} value={name}>
                  {name}
                </option>
              ))}
            </select>
          </div>
          <div className={styles.formGroup}>
            <label htmlFor="amount" className={styles.label}>
              :
            </label>
            <input
              type="number"
              id="amount"
              value={amount}
              onChange={handleAmountChange}
              placeholder=""
              required
              className={styles.input}
              disabled={isLoading}
            />
          </div>
          <button 
            type="submit" 
            className={styles.button}
            disabled={isLoading}
          >
            {isLoading ? '...' : ''}
          </button>
        </form>
      </div>

      <div className={styles.card}>
        <h2 className={styles.title}></h2>
        <div className={styles.transactionList}>
          {renderTransactionHistory()}
        </div>
      </div>

      <div className={styles.card}>
        <h2 className={styles.title}></h2>
        {message && <p className={styles.message}>{message}</p>}
      </div>
    </div>
  );
}

